let Global = require('Global');
let GameState = require("GameState");
let GameConfig = require("GameConfig");

cc.Class({

    extends:cc.Component,
    properties:{

        ScoreLable:cc.Label,
        HighLable:cc.Label,
        TimeLable:cc.Label,
        clockTime:5,
        clockNode:cc.Node,
        NormalNode:cc.Node,
        NewHighNode:cc.Node,
        gamecontroller:require('gamecontroller'),
        newHightScoreLb:cc.Label,
        newHightHistoryLb:cc.Label,
        closeBtn:cc.Node
    },

    onLoad:function () {
        
    },

    onEnable:function () {


        let score =  Global.score;
        this.newRecord = score;

        let highScore =  Global.highScore;
        this.ScoreLable.string = score.toString();
        this.HighLable.string = highScore.toString();
        this.TimeLable.string =  this.clockTime.toString();
        Global.newHistory(score);

        this.gamecontroller.gameOver();

        //如果不是最新高分。正常界面，开始倒计时
        //如果新的最高分,展现最高分.关闭后，开始倒计时
        this.clockNode.active  = false
        if(score > highScore){
            this.NewHighNode.active = true;
            this.NormalNode.active = false;
            this.newHightScoreLb.string = score.toString();
            this.newHightHistoryLb.string = highScore.toString();
            this.closeBtn.active = false;
            this.scheduleOnce(this.closeBtnShow,0.2);
        }else{
            this.NewHighNode.active = false;
            this.NormalNode.active = true;
            this.showAdBtn(true);
        }

        this.schedule(this.clockAnimate,0.8);

        if(score > Global.thisscore){
            Global.thisscore = score;
            if(Global.shareIndex < Global.shareCount){
                var FBP = require("FBPlugin");
                var str = " get a score of "+ Global.thisscore.toString() +  " in Tank and Fire！";
                if(GameConfig.isFBInstantGame()){
                    FBP.updateAsync(str);
                }
                Global.shareIndex++;
            }
        }

    },

    showAdBtn:function (show) {

        if(Global.showAdTimes == 0){
            this.clockNode.active  = show;
            this.schedule(this.eventClock,1);
            Global.showAdTimes++;
        }
    },

    eventClock:function () {
        this.clockTime --;
        if(this.clockTime <= 0){
            this.clockNode.active  = false;
            this.gamecontroller.updateState(GameState.over);
        }
        this.TimeLable.string =  this.clockTime.toString();

    },

    closeBtnShow:function () {

        this.closeBtn.active = true;
    },

    clockAnimate:function () {

        let sc1 = cc.scaleTo(0.15,1.25,1.25);
        let sc2 = cc.scaleTo(0.3,0.95,0.95);
        let seq = cc.sequence(sc1,sc2);
        this.clockNode.runAction(seq);
    },
    
    onClock:function () {
        this.unschedule(this.eventClock);

        //
        this.gamecontroller.updateState(GameState.playing);
        this.gamecontroller.tankController.resetTanks();
        this.gamecontroller.moreLife();
        this.node.active = false;

    },

    onContinue:function () {

        //所有的数据进行重制。clockAnimate

        let LineController  = require("LineController");
        LineController.reset();
        Global.initHistory();

        cc.director.loadScene("start");
    },

    onShare:function (event, intent) {
        let FBP = require("FBPlugin");
        FBP.chooseAsync();
    },


    onHightShare:function (event) {
        let FBP = require("FBPlugin");
        let self = this;
        FBP.chooseAsync(function () {
            let FBP2 = require("FBPlugin");
            let str = " get a new RECORD of "+ self.newRecord.toString() + "！Can you get more?";
            FBP2.updateHighAsync(str,self.onCloseHigh.bind(self));
        });
    },
    
    onCloseHigh:function () {
        this.NewHighNode.active = false;
        this.NormalNode.active = true;
        if(Global.showAdTimes == 0){
            this.showAdBtn(true);
        }
    },

});