let Global = require('Global');
let GameState = require("GameState");

cc.Class({

    extends:cc.Component,
    properties:{

    },

    onShare:function (event, intent) {
        let FBP = require("FBPlugin");
        FBP.shareFb('SHARE');
    },



    
    onChooseAsync:function (event, intent) {
        let FBP = require("FBPlugin");
        FBP.chooseAsync();
    },

    onUpdateAsync:function (event, intent) {
        let FBP = require("FBPlugin");
        var str = " get a score of 99  in Tank and Fire！";
        FBP.updateAsync(str);
    },

    onHightShare:function (event, intent) {
        let FBP = require("FBPlugin");
        var str = " get a score of 100 " +  " in Tank and Fire！";
        FBP.updateHighAsync(str);
    },

    oncreateShortCut:function (event, intent) {
        let FBP = require("FBPlugin");
        FBP.createShortCut();
    },


});