// game art from : https://openpixelproject.itch.io/opp2017sprites
const CameraControl = require('CameraControl');

cc.Class({
    extends: cc.Component,

    properties: {
        maxSpeed: 500,
        camera: cc.Camera,

    },

    // use this for initialization
    onLoad: function () {
        this.speed = 300;
    },


    // called every frame, uncomment this function to activate update callback
    update: function (dt) {
        // console.log("dist:",dt,dt*this.speed);
        this.node.setPositionY(this.node.getPositionY()+dt*this.speed);
        if(this.camera){
            this.camera.node.setPositionY(this.node.getPositionY()+dt*this.speed);
        }
    },
});
