cc.Class({
    extends:cc.Component,

    properties:{
        blocksNodes:cc.Node
    },

    onEnable:function () {

        // let ret= [];
        // ret.push(null);
        // ret.push(1);
        // ret.push(2);
        // ret.push({name:'bing'});
        // console.log(ret);
        // console.log(ret[0]);
        // console.log(ret[1]);

        this.testYIndex = 0;

        let Factory = require("LineFactory");

        //block
        if(true){
            for (let line = 0; line< 10;line++){
                let objs = Factory.create('new','1',line);
                if(objs['b'].length >0){
                    for(let i = 0; i < objs['b'].length; i++){
                        let tank = objs['b'][i];
                        if(tank){
                            this.blocksNodes.addChild(tank);
                            // tank.position.y = -440+line*140;
                            tank.setPositionY(440+line*140);
                        }
                    }
                }
            }
        }

        //bar
        if(true){
            for (let line = 10; line< 15;line++){
                let objs = Factory.create('new','1',line);
                if(objs['bar'].length >0){
                    for(let i = 0; i < objs['bar'].length; i++){
                        let bar = objs['bar'][i];
                        if(bar){
                            this.blocksNodes.addChild(bar);
                            bar.setPositionY(440+line*140);
                        }
                    }
                }
            }
        }

        // Factory.parseValue("1D2");

    },

    update:function (dt) {
        this.testYIndex++;
        if(this.testYIndex%100==0){
            // console.log("Index",this.testYIndex,this.blocksNodes.getPositionY());
        }

        if(this.blocksNodes.getPositionY()<(-2330)){
            this.blocksNodes.setPositionY(1330*0.5);
        }

    },
});