let Config = require("TankConfig");
let Tank = require("Tank");
let BingLog = require("BingLog");
let GameType = require("GameType");
let FC = cc.Class({
    
        ctor:function () {
            this.pool = new cc.NodePool();
        },
    
        initTank:function (id,head=false) {
            let config = Factory.getConfig(id);
            if(!config)
            {

                config = Factory.getConfig('1');
                BingLog.warn("this id can't be found ",id);
                // return null;
            }

            let prefabName =  "prefab/tankhead";
            let tankPrefab = cc.loader.getRes(prefabName);
            let tank= cc.instantiate(tankPrefab);
            let tankScript = tank.getComponent('Tank');
            tankScript.initTank(config);
            tank.getComponent('Tank').type = GameType.Tank;
            // console.log('tank',tank);
            return tank;
        },

        initTankBody:function (id,head=false) {
            let config = Factory.getConfig(id);
            if(!config)
            {

                config = Factory.getConfig('1');
                BingLog.warn("this id can't be found ",id);
                // return null;
            }

            let tank = this.pool.get();
            if(!tank){
                let prefab = cc.loader.getRes("prefab/tank");
                tank= cc.instantiate(prefab);
            }
            let tankScript = tank.getComponent('Tank');
            tankScript.initTank(config);
            tank.getComponent('Tank').type = GameType.Tank;
            // console.log('tank',tank);
            return tank;
        },


        //字符串id.
        createTankHead:function (id) {
            let tank= Factory.initTank(id,true);
            if(tank){
                let tankScript = tank.getComponent('Tank');
                tankScript.updateHead();
                tank.name = 'head';
                tankScript.index = 0;

            }
            return tank;
        },


        //字符串id.
        createTankBody:function (id) {
            let tank= Factory.initTankBody(id);
            if(tank){
                let tankScript = tank.getComponent('Tank');
                tankScript.updateBody();
                // tank.name = 'body';
                tankScript.index = 1;
            }
            return tank;
        },

        put:function (node) {

            let putnode =node.getComponent("GameComponent");
            // console.log("type",putnode.type);
            if(putnode.type!= GameType.Tank){
                debugger;
            }
            this.pool.put(node);
        },

        //字符串id.
        getConfig:function (id) {
            let config = Config[id];
            if(!config){
                return null;
            }
            else{
                return config;
            }

        }

});

let Factory = new FC();

module.exports = Factory;
