let BingLog = require("BingLog");

let GameComponent = require("GameComponent");
let GameType = require("GameType");
let SpriteFrameCenter = require('SpriteFrameCenter');

let Bullet = cc.Class({

    extends:GameComponent,

    statics:{
        MaxDist:cc.winSize.height*0.5 - 20,
        Index:0,
    },
    properties:{
        sp:cc.Sprite,
        type:{
            default:GameType.Bullet,
            override:true,
            visible:false
        },
        totalDist:0,
        speed : 300,
        relativespeed:100 //相对tank速度。
    },

    init:function (spName,speed,relativespeed) {
        this.type = GameType.Bullet;
        this.speed = speed;
        this.relativespeed =relativespeed;
        this.spName = spName;
        BingLog.log("Bullet init:");
        this.node.name = 'bullet' + Bullet.Index.toString();
        Bullet.Index++;
    },

    onLoad:function () {
        this.type = GameType.Bullet;
        if(this.spName){

            // let pngname = "png/" + this.spName;
            // let self=this;
            // cc.loader.loadRes(pngname, cc.SpriteFrame, function (err, spriteFrame) {
            //     self.sp.getComponent(cc.Sprite).spriteFrame = spriteFrame;
            // });

            // var realUrl = cc.url.raw("resources/png/" + this.spName);
            // var texture = cc.textureCache.addImage(realUrl);
            // let spFrame = new cc.SpriteFrame(texture);
            // this.sp.spriteFrame = spFrame;

            // this.sp.spriteFrame = SpriteFrameCenter.getFrameByName("resources/png/tank/" + this.spName);
            this.sp.spriteFrame = SpriteFrameCenter.getFrameFromAtlas("png/tanks",this.spName);
        }
    },


    //ret true .删除。 false 没有删除。
    updateNode:function (dt) {
        // let dist = dt*this.relativespeed;
        // this.totalDist += dist;  //相对距离用来消失。

        let y =  this.node.getPositionY() + this.speed;
        this.node.setPositionY(y);
    },

    // checkRemovable:function () {
    //     if(this.totalDist >= Bullet.MaxDist){
    //         return true;
    //     }
    //     return false;
    // }

});

module.exports = Bullet;