let Config = require("BlockConfig");
let GameType = require("GameType");
let FC = cc.Class({
    extends:cc.Object,

    ctor:function () {
        this.pool = new cc.NodePool();
        console.log("Factory should be call once!!");
    },

    init:function (count) {

        let newNode = this.pool.get();
        if(!newNode){
            let prefab = cc.loader.getRes("prefab/block");
            newNode= cc.instantiate(prefab);
        }
        let script = newNode.getComponent('Block');
        script.init(count);
        script.updateNode();
        newNode.getComponent('Block').type =  GameType.Block;
        return newNode;
    },

    put:function (node) {

        let putnode =node.getComponent("GameComponent");
        // console.log("type",putnode.type);
        if(putnode.type!= GameType.Block){
            debugger;
        }
        this.pool.put(node);
    },

    //
    create:function (count) {
        let obj= Factory.init(count);
        return obj;
    },

    //
    getConfig:function (id) {
        let config = Config[id];
        if(!config){
            return null;
        }
        else{
            return config;
        }

    },
    
    preCreat:function () {
        let countLimited = [5,12,25,40,50];
        let ret = [];
        for(let i = 0; i < countLimited.length; i++){
            let newNode = this.create(countLimited[i]);
            this.put(newNode);
            // ret.push(newNode);
        }
        return ret;
    }
});

let Factory = new FC();
module.exports = Factory;

