let GameType = require("GameType");
let FC = cc.Class({

    ctor:function () {
        this.pool = new cc.NodePool();
    },

    init:function (id,count) {

        let newNode = this.pool.get();
        if(!newNode){
            let prefab = cc.loader.getRes("prefab/item");
            newNode= cc.instantiate(prefab);
        }
        let item = newNode.getComponent("Item");
        item.updateItem(id,count);
        return newNode;
    },

    put:function (node) {
        let putnode =node.getComponent("GameComponent");
        // console.log("type",putnode.type);
        if(putnode.type!= GameType.Item){
            debugger;
        }
        this.pool.put(node);
    },

    //
    create:function (id,count) {
        let obj= Factory.init(id,count);
        return obj;
    },


});

let Factory = new FC();
module.exports = Factory;