cc.Class({
    extends:cc.Component,

    onEnable:function () {

        let BarFactory = require("BarFactory");
        let bar = BarFactory.create();
        if(bar){
            console.log("creat bar  OK!");
            this.node.addChild(bar);
            bar.position = cc.p(-200,-100);

        }else{
            console.log("creat bar failed!");
        }

        let BulletFactory = require("BulletFactory");
        let bullet = BulletFactory.create();
        if(bullet){
            console.log("creat bullet  OK!");
            this.node.addChild(bullet);
            bullet.position = cc.p(-100,-150);

        }else{
            console.log("creat bullet failed!");
        }


    }
});