let Config = require("LineConfig");
let TankConfig = require("TankConfig");
let BingLog = require("BingLog");
let GU = require('GameUtils');
let GameType = require('GameType');
let ItemFactory = require("ItemFactory");
let BlockFactory = require("BlockFactory");
let BarFactory = require("BarFactory");

let Factory = cc.Class({
    extends:cc.Object,
    statics: {

        //根据配置生成对象。
        create: function (hard,groupid, lineid) {
            let obj = Factory.parseLine(hard,groupid, lineid);
            let ret = {'b':[],'bar':[],'i':[],type:obj.type}; //三部分。block bar  item


            if (obj) {
                BingLog.log("create ",obj);
                BingLog.log("create type ",obj.type,obj['type']);

                ret['i'] = Factory.createItems(obj['i']);
                switch (obj.type){
                    case 0:{
                        break;
                    }
                    case 1:{
                        ret['b'] = Factory.createBlocks(obj['b']);
                        break;
                    }
                    case 2:
                    {
                        ret['bar'] = Factory.createBars(obj['bar']);
                        break;
                    }
                    default:
                        break;
                }
            }
            return ret;
        },


        put:function (node) {
            let childrens = node.getChildren();
            let count =  childrens.length;
            for(let i = count-1 ; i > -1;--i){
                let sonNode =  childrens[i];
                if(sonNode){
                    let type = sonNode.getComponent('GameComponent').type;
                    if(type == GameType.Block){
                        BlockFactory.put(sonNode);
                    }else if(type == GameType.Bar){
                        // BlockFactory.put(sonNode);
                        BarFactory.put(sonNode);
                    }else if(type == GameType.Item){
                        ItemFactory.put(sonNode);
                    }
                }
            }
            node.removeFromParent(true);
        },

            //入参items [{item:true,value:{itemid:itemid,count:randomV}},{item:false,value:0}]
        createItems:function (items) {
            let ret = [];
            for(let i = 0; i < items.length; i++){
                let itemConfig = items[i];
                if(itemConfig.item)
                {
                    let value  = itemConfig.value;
                    let block = ItemFactory.create(value.itemid,value.count);
                    block.position = cc.p( -2.5*140 + i*140+70,0);
                    ret.push(block);
                }
                else{
                    ret.push(null);
                }
            }
            return ret;
        },

        createBlocks:function (blocks) {


            let ret = [];
            for(let i = 0; i < blocks.length; i++){
                let blockCount = blocks[i];
                if(blockCount >0)
                {
                    let block = BlockFactory.create(blockCount);
                    block.position = cc.p( -2.5*140 + i*140+70,0);
                    ret.push(block);
                }
                else{
                    ret.push(null);
                }
            }
            return ret;
        },

        createBars:function (bars) {


            let ret = [];
            for(let i = 0; i < bars.length; i++){
                if(bars[i] ==1)
                {
                    let bar = BarFactory.create();
                    bar.position = cc.p(-2.5*140 + i*140+70,0);
                    ret.push(bar);
                }
                else{
                    ret.push(null);
                }
            }
            return ret;
        },


        //
        getConfig: function (id) {
            let config = Config[id];
            if (!config) {
                return null;
            }
            else {
                return config;
            }

        },

        //parse ith group，jth line，create
        // group :string line : number
        //
        //返回
        // {
        // type:0,
        // b:[0,2,1,2,3]
        // bar:[0,1,1,1,0],
        // i:[{item:true,value:{itemid:itemid,count:randomV}},{item:false,value:0}]
        // true->表示有item，后面是配置。false->表示没有item,后面值忽略。
        // }
        parseLine:function (hard,groupid,lineid) {

            let group = Config[hard][groupid];
            if(!group){
                BingLog.warn("the groupid can't be found:",groupid);
                return {};
            }
            let line = group[lineid];
            if(!line){
                BingLog.warn("the line can't be found:",lineid);
                return {};
            }

            let ret = {};
            let type =  line['type'];
            ret['type'] = type;
            ret['bar']=[];
            ret['b']=[];
            ret['i'] = Factory.parseItem(line['i']);
            switch (type){
                case 0: //空
                    break;
                case 1://block
                    ret['b'] = Factory.parseBlock(line['b']);
                    break;
                case 2:
                    ret['bar'] = line['bar'];
                    break;
                default:
                    break;
            }
            return ret;
        },

        //unused
        parseBar:function (barConfig) {
            
        },
        
        parseItem:function (config) {
            // '10001;1D2','0','0','10001;1D2','0'
            let len = config.length;
            let ret = [];
            for(let i = 0; i< len;++i){
                let blockItem = config[i];
                if(blockItem == '0'){
                    ret.push({item:false,value:0});
                }
                else{
                    let icons = blockItem.split(";");
                    let itemid = icons[0];
                    let randomV = Factory.parseValue(icons[1]);
                    ret.push({item:true,value:{itemid:itemid,count:randomV}});
                }
            }
            return ret;
        },

        //parse string like '1D2',return a random value.
        parseValue:function (itemStr) {
            let randoms = itemStr.split("D");
            // BingLog.log('randoms: ',randoms);
            let count = Number.parseInt(randoms[0]);
            let max = Number.parseInt(randoms[1]);
            let start = count;
            let end = count*max;
            let randomV = GU.randomInt(start,end);
            return randomV;
        },
        parseBlock:function (blockConfig) {
            // ['1D2','1D2','1D2','1D2','1D2']
            // ['0','0','0','0','0']
            let len = blockConfig.length;
            let ret = [];
            for(let i = 0; i< len;++i){
                let blockItem = blockConfig[i];
                if(blockItem == '0'){
                    ret.push(0);
                }
                else{
                    let randomV = Factory.parseValue(blockItem);
                    ret.push(randomV);
                }
            }
            return ret;
        },
        
        

    }

});

module.exports = Factory;
