let GameState = require("GameState");
let LineController = require('LineController');


const LineInitCount = 10; //初始化10行。
const FirstDist = 800;
let BlockFactory =  require("BlockFactory");
let LineFactory =  require("LineFactory");
let ItemFactory = require("ItemFactory");
let ParticleSystemCenter = require("ParticleSystemCenter");

cc.Class({
    extends: cc.Component,

    properties: {

        gamecontroller:require("gamecontroller"),
        gameNodes:cc.Node, //存放block bar item
        // tankNode:cc.Node, //全屏幕,
        dist:0,
        totaltime:0,
        lineIndex:0,
        first:true,
    },

    // use this for initialization
    onLoad: function () {
        this.init();
    },


    init:function () {

        this.lineNodeInfo = {};
        let initLines = 10;
        this.addLines(initLines,true);
        this.addLines(0);
    },

    addLines:function (count,init=false) {

        let ret = null;
        if(init){
            ret = LineController.initFirstLines(count);
        }else{
            ret = LineController.addLines(count);
        }

        for (let line = 0; line < count;line++){
            let data = ret[line];
            let node = data['linenode'];
            node.name = this.lineIndex.toString();
            this.lineNodeInfo[this.lineIndex] = data;
            // console.log("befor:",node.getPositionY(),this.lineIndex*140+70);
            node.setPositionY(0 + this.lineIndex*140+70);
            // node.setPositionY(-500);
            // console.log("after:",node.getPositionY());
            this.lineIndex++;
            this.gameNodes.addChild(node);

            // this.camera.addTarget(node);

            // console.log("nod set",node.getPosition());
            // console.log("lineArray Index",line,node.convertToWorldSpace(node.getPosition()));
            // console.log("lineArray Index",line,node.convertToWorldSpaceAR(node.getPosition()));
        }

    },


    //下落一定距离，进行删除最下面一行，在最上面添加一个新行
    updateBlocksNode:function () {

        console.log("updateBlocksNode");
        let len = this.gameNodes.childrenCount;
        if(len < 1)
        {
            return;
        }
        let mostBelowPtY = this.gameNodes.children[0].getPositionY();
        let deleteNode = null;
        for(let i = 0;i<len;++i){
            let node = this.gameNodes.children[i];

            //删除非tank的
            if(node.name != 'head' &&
                node.name != 'body'
            ){
                let pty = node.getPositionY();
                // console.log("line position y",i, pty);
                if(mostBelowPtY >= pty){
                    mostBelowPtY=pty;
                    deleteNode = node;
                }
            }
        }
        //如果deleteNode已经超出屏幕边界，进行删除
        // debugger;
        if(deleteNode){
            let pt = deleteNode.getPosition();
            // console.log("delete node pt:",pt);
            // console.log("will remove ",deleteNode.name);

            // deleteNode.removeFromParent(true);
            // BlockFactory.put(deleteNode);
            LineFactory.put(deleteNode);
            delete this.lineNodeInfo[deleteNode.name];
            this.addLines(1);
        }

    },


    //删除某个buff的方块 //
    deletItemNodeAt:function (item,lineIndex,collumn) {
        let lineInfo = this.lineNodeInfo[lineIndex];
        let childs = lineInfo['child'];
        if(childs.length > 0){
            childs[collumn][2] = null;
        }

        ItemFactory.put(item.node);
        // item.node.removeFromParent(true);
    },

    //碰撞Block
    // removeall 不论上面的数字，直接进行清空。
    //blockInfo 格式 {type:GameType.Empty,collumn:collumn,lineIndex:lineIndex};
    collisionBlock:function (blockInfo,subCount, removeall = false) {
        let blockNode = this.lineNodeInfo[blockInfo.lineIndex]['child'][blockInfo.collumn][0];
        let removalbe = blockNode.getComponent("Block").updateCount(subCount,removeall);
        let ptX = blockNode.getPositionX();
        let ptY = this.lineNodeInfo[blockInfo.lineIndex]['linenode'].getPositionY();
        // let worldpt = blockNode.convertToWorldSpaceAR(pt);
        let block = blockNode.getComponent('Block');
        let file = block.particleFile;
        if(removalbe){
            BlockFactory.put(blockNode);
            //进行删除这个block
            // let block = this.lineNodeInfo[blockInfo.lineIndex];
            this.lineNodeInfo[blockInfo.lineIndex]['child'][blockInfo.collumn][0] = null;
            ParticleSystemCenter.addParticleForNode(file,this.gamecontroller.particleNode, cc.p(ptX,ptY));
        }else{

            block.collisionAnimate();
        }

        this.gamecontroller.updateScore();
    },


    // called every frame, uncomment this function to activate update callback
    update: function (dt) {

    },
});
