cc.Class({
    extends:cc.Component,
    properties:{
        view:require('MsgView')
    },

    onLoad:function () {
        
    },

    onTest:function () {

        FBInstant.canCreateShortcutAsync()
            .then(function(canCreateShortcut) {
                if (canCreateShortcut) {
                    view.showMsg("can create");
                    FBInstant.createShortcutAsync()
                        .then(function() {
                            // Shortcut created
                        })
                        .catch(function() {
                            // Shortcut not created
                        });
                }else{
                    view.showMsg("can create");
                }
            });

    }

});