let GameType = require("GameType");
let FC = cc.Class({
    extends:cc.Object,
    ctor:function () {
        this.pool = new cc.NodePool();
    },

    init:function (spName,speed,relativespeed) {

        let newNode = this.pool.get();
        if(!newNode){
            let prefab = cc.loader.getRes("prefab/bullet");
            newNode= cc.instantiate(prefab);
        }
        let Bullet = newNode.getComponent("Bullet");
        Bullet.init(spName,speed,relativespeed);
        return newNode;
    },

    put:function (node) {

        let putnode =node.getComponent("GameComponent");
        // console.log("type",putnode.type);
        if(putnode.type!= GameType.Bullet){
            debugger;
        }
        this.pool.put(node);
    },

    create:function (spName,speed,relativespeed) {
        let obj= Factory.init(spName,speed,relativespeed);
        return obj;
    },

});

let Factory = new FC();
module.exports = Factory;

