let GameType = require("GameType");
let FC = cc.Class({


        ctor:function () {
            this.pool = new cc.NodePool();
        },

        init:function () {

            let newNode = this.pool.get();
            if(!newNode){
                let prefab = cc.loader.getRes("prefab/bar");
                newNode= cc.instantiate(prefab);
            }
            newNode.getComponent("Bar").type = GameType.Bar;
            return newNode;
        },

        put:function (node) {
            let putnode =node.getComponent("GameComponent");
            // console.log("type",putnode.type);
            if(putnode.type!= GameType.Bar){
                debugger;
            }
            this.pool.put(node);
        },

        //
        create:function () {
            let obj= Factory.init();
            return obj;
        },



});

let Factory = new FC();
module.exports = Factory;