let BingLog = require("BingLog");

let GameComponent = require("GameComponent");
let GameType = require("GameType");
let SpriteFrameCenter = require('SpriteFrameCenter');
//onepos [4.5]
//twopos [3,6]

let Tank = cc.Class({
    extends:GameComponent,

    statics:{
      Index:0,
    },
    properties:{
        type:{
            default:GameType.Tank,
            override:true,
            visible:false
        },
        sp:cc.Sprite,
        wudisp:cc.Sprite,
        label:cc.Label,
        typeid:1,
        config:{
            type:Object,
            default:null,
            visible:false
        },
        index:0,
        points:[], //移动的点。
        twopos:[], //one子弹位置。
        onepos:[], //two 子弹的位置
        particleFile:"",
    },

    initTank:function (config) {
        this.config = config;
        this.points.length = 0;
        this.type = GameType.Tank;
        this.twopos.push(...config.twopos);
        this.onepos.push(...config.onepos);
        this.particleFile = this.config.texiao;
        BingLog.log("tanke initTank:",this.config);
    },

    onLoad:function () {
        BingLog.log("tanke onLoad");
    },

    updateHead:function () {
        if(!this.config){
            BingLog.warn("the tank update head need init its config before.");
            return;
        }
        // let pngname = "png/" + this.config.head;


        // let self=this;
        // cc.loader.loadRes(pngname, cc.SpriteFrame, function (err, spriteFrame) {
        //     self.sp.getComponent(cc.Sprite).spriteFrame = spriteFrame;
        // });

        // cc.loader.loadRes("png/" + this.config.wudi, cc.SpriteFrame, function (err, spriteFrame) {
        //     self.wudisp.getComponent(cc.Sprite).spriteFrame = spriteFrame;
        // });


        // var realUrl = cc.url.raw("resources/png/tank/" + this.config.head);
        // var texture = cc.textureCache.addImage(realUrl);
        // let spFrame = new cc.SpriteFrame(texture);
        // this.sp.spriteFrame = spFrame;
        //
        // realUrl = cc.url.raw("resources/png/tank/" + this.config.wudi);
        // texture = cc.textureCache.addImage(realUrl);
        // spFrame = new cc.SpriteFrame(texture);
        // this.wudisp.spriteFrame = spFrame;

        this.sp.spriteFrame = SpriteFrameCenter.getFrameFromAtlas("png/tanks",this.config.head);
        this.wudisp.spriteFrame = SpriteFrameCenter.getFrameFromAtlas("png/tanks",this.config.wudi);

    },

    updateBody:function () {
        if(!this.config){
            BingLog.warn("the tank update boby need init its config before.");
            return;
        }
        this.node.name = "body" + Tank.Index.toString();
        Tank.Index++;

        let len = this.config.body.length;
        let GU = require("GameUtils");
        let index = GU.random(len-1);
        // let pngname = "png/" + this.config.body[index];

        // let self=this;
        // cc.loader.loadRes(pngname, cc.SpriteFrame, function (err, spriteFrame) {
        //     self.sp.getComponent(cc.Sprite).spriteFrame = spriteFrame;
        // });

        // var realUrl = cc.url.raw("resources/png/" + this.config.body[index]);
        // var texture = cc.textureCache.addImage(realUrl);
        // let spFrame = new cc.SpriteFrame(texture);
        // this.sp.spriteFrame = spFrame;

        // this.sp.spriteFrame = SpriteFrameCenter.getFrameByName("resources/png/tank/" + this.config.body[index]);

        this.sp.spriteFrame = SpriteFrameCenter.getFrameFromAtlas("png/tanks",this.config.body[index]);
    },



    moveNext:function () {
        let pt = null;
        if(this.points.length>0){
            pt = this.points.pop();
            this.node.setPosition(this.points[this.points.length-1]);
        }else{
            this.points.push(this.node.getPosition());
            pt = this.node.getPosition();
        }
        return pt;
    },

    addNextPt:function (pt) {
        this.points.unshift(pt);
    },

    //取平均值。保留count个点
    updatePoints:function (count) {
        let max = this.points[0];
        let min = this.points[this.points.length-1];
        let distx = (max.x - min.x)/count;
        let disty = (max.y - min.y)/count;
        let tmp = [];
        for(let i =0; i < count;++i){
            tmp.push(cc.p(min.x+distx*i,min.y+disty*i));
        }
        this.points.length=0;
        this.points =[];
        this.points.push(...tmp);
    },

    moveAction: function moveAction(dt, pt,topPt) {
        var mv = cc.moveTo(dt, pt);
        // var fun = cc.callFunc(this.removeNode.bind(this),topPt);
        // var seq = cc.sequence(mv,fun);
        this.node.runAction(mv);
    },

    openInvincible:function (open = false) {
        if(open){
            this.schedule(this.runInvincible,0.5);
            this.wudisp.node.active = true;
        }else{
            this.unschedule(this.runInvincible,0.5);
            this.wudisp.node.active = false;
        }
    },

    runInvincible:function () {
        this.wudisp.node.active = !this.wudisp.node.active;
    },


    isCollision:function () {

        return this.collision;
    },
    openCollision:function (open = false,invincible = false) {
        this.collision = open;
        if(open){
            let time  =  0.133;
            if (invincible){
                time = time/1.5;
            }
            this.schedule(this.runCollision,time);
            this.sp.node.active = false;
        }else{
            this.unschedule(this.runCollision);
            this.sp.node.active = true;
        }
    },

    runCollision:function () {
        this.sp.node.active = !this.sp.node.active;
    }


});

module.exports = Tank;