let BingLog = require("BingLog");
let GameComponent = require("GameComponent");
let GameType = require("GameType");
let ItemType = require("ItemType");
let SpriteFrameCenter = require('SpriteFrameCenter');
let Global = require("Global");

cc.Class({
    extends:GameComponent,

    properties:{
        sp:cc.Sprite,
        label:cc.Label,   //只有tankbody才会有用。
        type:{
            default:GameType.Item,
            override:true,
            visible:false
        },
        count:0, //只有tankbody才会有用。
        time:0, //无敌。
        subType:{
            default:ItemType.Tank,
            override:true,
            visible:false
        },

    },

    //count
    init:function () {
        BingLog.log("Item init:");
    },

    onLoad:function () {
        // BingLog.log("tanke onLoad");
        // this.updateNode();

    },
    
    updateItem:function(id,count) {

        let ItemConfig = require("ItemConfig");
        let config = ItemConfig[id];
        this.type = GameType.Item;
        this.subType = config.type;
        this.count = count;
        this.time = config.time;
        //

        if(this.subType == ItemType.TankBody){
            let TankConfig = require("TankConfig");
            let tc = TankConfig[Global.tankid];
            this.updateNode(tc.itemIcon);
            this.label.string = this.count.toString();

        }else{
            this.updateNode(config.icon);
            this.label.string = "";
        }
    },

    updateNode:function (pngname) {
        // let self=this;

        // var realUrl = cc.url.raw("resources/png/" + pngname);
        // var texture = cc.textureCache.addImage(realUrl);
        // let spFrame = new cc.SpriteFrame(texture);
        // this.sp.spriteFrame = spFrame;

        // this.sp.spriteFrame = SpriteFrameCenter.getFrameByName("resources/png/item/" + pngname);

        this.sp.spriteFrame = SpriteFrameCenter.getFrameFromAtlas("png/game",pngname);

        // cc.loader.loadRes('png/'+pngname, cc.SpriteFrame, function (err, spriteFrame) {
        //     self.sp.getComponent(cc.Sprite).spriteFrame = spriteFrame;
        // });
    }
});