//负责产生block相关数据

let LineFactory = require("LineFactory");
let GU = require('GameUtils');
let PositionConfig = require("PositionConfig");
let SpeedConfig = require("SpeedConfig");
let LineConfig = require("LineConfig");

module.exports = function () {

    const ArraySize = 12;
    const MaxGroup = 6;
    let plugin = cc.Class({
        extends:cc.Object,
        properties:{
            hard:'new', //游戏难度 new freshman  m  medium  h hard
            groupid:1,
            lineid:0,
            lineArray:[], //每次都是从头进行
            // getLineIndex:0,  //从LineArray中取值的次序。
            lineCount:0, //用来控制难度的。
        },

        ctro:function () {
            // this.init();
        },

        reset:function () {
            this.hard = 'new';
            this.updateGroup();
            this.lineid = 0;
            this.lineCount =0;
            this.groupid = '1';
        },

        //初始化的空行。
        initFirstLines:function (count = 6) {
            this.hard = 'init';
            this.lineid = 0;
            this.groupid = '1';
            let ret = this.createLines(this.lineid,this.lineid+count);
            this.hard = 'new';
            return ret;
        },

        //行数控制速度。
        getSpeedByConfig:function () {
            let linecount =  this.lineCount - 5;
            let lines = SpeedConfig.dist;
            // let tmp = lines[lines.length-1];
            for(let i = lines.length-1; i > -1; --i){
                let line = lines[i];
                if(linecount >= line){
                    return SpeedConfig['speed'][i];
                }
            }
            return SpeedConfig['speed'][0];
        },

        getLindeCount:function () {
            let lines = LineConfig[this.hard][this.groupid]
            return lines.length;
        },
        
        //每次count不应该超过MaxLine
        addLines:function (count) {

            let MaxLine = this.getLindeCount();
            this.lineCount +=count;
            console.log("add line:",this.hard,this.groupid,this.lineid,count);
            //避免越界。
            if(this.lineid >= MaxLine){
                this.lineid = this.lineid%MaxLine;
                //重新随机group
                this.updateGroup();
                this.lineid = 0 ;
            }

            if(this.lineid+count<=MaxLine)
            {
                let ret = this.createLines(this.lineid,this.lineid+count);
                this.lineid +=count;
                return ret;
            }
            else{
                let ret = this.createLines(this.lineid,MaxLine);
                //重新随机group
                this.updateGroup();
                this.lineid = 0 ;
                let left = this.lineid+count-MaxLine;
                let ret2 = this.createLines(this.lineid,left);
                ret.push(...ret2);
                return ret;
            }

            this.updateHard();
        },

        updateHard:function () {

            let keys = PositionConfig.keys;
            for(let i = 0; i<keys.length;++i){
                let config = PositionConfig[key];
                if(config['min']<= this.lineCount &&
                    config['max']>= this.lineCount
                ){
                    this.hard = key;
                    break;
                }
            }

            this.lineid=0;
            this.updateGroup();
        },

        updateGroup:function () {
            let random = GU.randomInt(1,MaxGroup);
            if(parseInt(this.groupid) == random){
                this.updateGroup();
            }else {
                this.groupid = random.toString();
            }
        },

        //添加几行。
        //返回
        // {
        //  linenode:node,
        //  child:[node,null,node] //null也需要添加 .Child保证每行5个，即使null。
        // }
        createLines:function (lineFrom,lineTo) {

            if(lineFrom == lineTo){
                return [];
            }

            let ret = [];
            for (let line = lineFrom; line < lineTo;line++){
                let objs = LineFactory.create(this.hard,this.groupid,line);

                //block
                var node = new cc.Node();
                //同一个地方可用存放多个节点信息。
                let lineNodeData = {linenode:node,child:[[],[],[],[],[]]};
                ret.push(lineNodeData);
                if(objs['b'].length >0){
                    for(let i = 0; i < objs['b'].length; i++){
                        let block = objs['b'][i];
                        if(block){
                            node.addChild(block);
                            lineNodeData.child[i].push(block);
                        }else{
                            lineNodeData.child[i].push(null);
                        }
                    }
                }else {
                    for (let i =0;i < 5; ++i){
                        lineNodeData.child[i].push(null);
                    }
                }

                //bar
                if(objs['bar'].length >0){
                    for(let i = 0; i < objs['bar'].length; i++){
                        let bar = objs['bar'][i];
                        if(bar){
                            node.addChild(bar);
                            lineNodeData.child[i].push(bar);
                        }else{
                            lineNodeData.child[i].push(null);
                        }
                    }
                }else {
                    for (let i =0;i < 5; ++i){
                        lineNodeData.child[i].push(null);
                    }
                }

                if(objs['i'].length >0){
                    for(let i = 0; i < objs['i'].length; i++){
                        let bar = objs['i'][i];
                        if(bar){
                            node.addChild(bar);
                            lineNodeData.child[i].push(bar);
                        }else{
                            lineNodeData.child[i].push(null);
                        }
                    }
                }else {
                    for (let i =0;i < 5; ++i){
                        lineNodeData.child[i].push(null);
                    }
                }
            }

            for(let i =0 ; i < ret[0].child.length; ++i){
                if(ret[0].child[i].length !=3 ){
                    console.error("child  in at line:",lineFrom,lineTo);
                    debugger;
                }
            }

            return ret;
        },

        init:function () {
           this.addLines(ArraySize);
        },

    });


    let p = new plugin();
    return p;

}();