module.exports = function () {

    //TODO::replace the image.
    let sharePng = "data:image/png;base64,";

    let newrecordPng = "data:image/png;base64,";

    let plugin =  cc.Class({
        extends:cc.Object,

        //intent  "INVITE" | "REQUEST" | "CHALLENGE" | "SHARE"
        shareFb:function (intent = 'REQUEST',callback) {

            FBInstant.shareAsync({
                intent: intent,
                image: sharePng,
                text: "It's a amazing game, play with me!",
                data: { myReplayData: '...' },
            }).then(function() {
                // continue with the game.
                if(callback)
                {
                    callback();
                }
            });
        },

        shareNewRecord:function (score,callback) {
            FBInstant.shareAsync({
                intent: "REQUEST",
                image: newrecordPng,
                text: 'I got new record ' + score.toString(),
                data: { myReplayData: '...' },
            }).then(function() {
                // continue with the game.
                if(callback)
                {
                    callback();
                }
            });
        },

        playerName:function () {
            var playerName = FBInstant.player.getName();

            return playerName;
        },

        chooseAsync:function (callback) {
            FBInstant.context
                .chooseAsync()
                .then(function() {
                    console.log(FBInstant.context.getID());
                    if(callback){
                        callback();
                    }
                });
        },

        updateAsync:function (text,callback) {
            FBInstant.updateAsync({
                action: 'CUSTOM',
                cta: 'Play',
                image: sharePng,
                text: {
                    default: FBInstant.player.getName() + text,
                },
                template: 'play_turn',
                data: { myReplayData: '...' },
                strategy: 'IMMEDIATE',
                notification: 'NO_PUSH',
            }).then(function() {
                console.log('Message was sent successfully');
                if(callback){
                    callback();
                }
            });
        },

        updateHighAsync:function (text,callback) {
            FBInstant.updateAsync({
                action: 'CUSTOM',
                cta: 'Play',
                image: newrecordPng,
                text: {
                    default: FBInstant.player.getName() + text,
                },
                template: 'play_turn',
                data: { myReplayData: '...' },
                strategy: 'IMMEDIATE',
                notification: 'NO_PUSH',
            }).then(function() {
                console.log('Message was sent successfully');
                if(callback){
                    callback();
                }
            });
        },

        createShortCut:function () {
            FBInstant.canCreateShortcutAsync()
                .then(function(canCreateShortcut) {
                    if (canCreateShortcut) {
                        FBInstant.createShortcutAsync()
                            .then(function() {
                                // Shortcut created
                            })
                            .catch(function() {
                                // Shortcut not created
                            });
                    }
                });
        }
    });


    let p = new plugin();
    return p;

}();