

var StateMachine = require('../utils/state-machine');
// var StateMachine = require('javascript-state-machine');
cc.Class({
    extends: cc.Component,


    // use this for initialization
    onLoad: function () {
        console.log("onload ",typeof StateMachine);
        var fsm = StateMachine.create({
            initial: 'green',
            events: [
                { name: 'warn', from: 'green', to: 'yellow' },
                { name: 'panic', from: 'yellow', to: 'red' },
                { name: 'calm', from: 'red', to: 'yellow' },
                { name: 'clear', from: 'yellow', to: 'green' }
            ],
            callbacks:{
                onpanic: function (event, from, to, msg) { console.log('panic! ' + msg); },
                onclear: function (event, from, to, msg) { console.log('thanks to ' + msg); },
                ongreen: function (event, from, to) { console.log('green'); },
                onyellow: function (event, from, to) { console.log('yellow'); },
                onred: function (event, from, to) { console.log('red'); },

                onwarn:function function_name(argument) {
                     console.log('onwarn');
                },

                onbeforewarn:function (argument) {
                    console.log('onbeforewarn123');
                }
            }
        });

        // fsm.onbeforewarn = function (argument) {
        //     console.log('onbeforewarn');
        // },

        fsm.onafterwarn = function (argument) {
            console.log('onafterwarn');
        },

        fsm.onleavegreen = function (argument) {
            console.log('onleavegreen');
        },

        fsm.onenteryellow = function (argument) {
            console.log('onenteryellow');
        },


        fsm.onbeforepanic = function(){
            console.log("onbeforepanic");
        }
        fsm.onleaveyellow = function(){
            console.log("onleaveyellow");
        }
        fsm.onenteryellow = function(){
            console.log("onenteryellow");
        }
        fsm.onafterpanic = function(event){
            console.log("onafterpanic",event);
        }

        fsm.warn(); //

        // fsm.panic('killer bees');
        // fsm.calm('sedatives in the honey pots');
    }
});