var BingLog = require("BingLog");


module.exports = function () {
    var LocalObjectPool = cc.Class({
        extends:cc.Object,

        ctor:function () {
            this.pools =  {};
        },

        getItemByName:function (name) {// 通过名字在缓存池中获取资源
            var pool = this.pools[name];
            if (!pool)
            {
                this.pools[name] = new cc.NodePool();
            }
            return pool.get();
        },

        putItemByName:function (name,node) {// 通过名字在缓存池中获取资源
            var pool = this.pools[name];
            if (!pool)
            {
                this.pools[name] = new cc.NodePool();
            }
            return pool.put(node);
        },

    });

    let _instance = new LocalObjectPool();
    return _instance;

}();