let GameConfig = require("GameConfig");
module.exports  =  function () {

    let lc = require("LocalStorage");
    const high = 'high';

    let cls = cc.Class({
        properties:{
            tankid:'1',
            score:0,
            thisscore:0, //这次游戏从完到结束的最高分数。
            shareCount:5, //每局normal分享总的次数。
            shareIndex:0, //每局分享
            highScore:0,
            showAdTimes:0,
            showRecord: false,
            loadCount:0, //1强制弹出choose
        },

        initHistory:function () {
            this.score = 0;
            this.showRecord = false;
            this.showAdTimes = 0;

            lc.get(high,0,function (v) {
                if (typeof v == "string"){
                    this.highScore = parseInt(v);
                }else if(typeof v == "number"){
                    this.highScore = v;
                }else{
                    this.highScore = 10000;
                }

            }.bind(this));

        },

        newHistory:function (h) {
            if(h>this.highScore){
                lc.set(high,h);
                this.highScore = h;
            }
        },

    });

    let instance = new cls();
    return instance;
}();