let Global = require('Global');
let SpriteFrameCenter = require('SpriteFrameCenter');

let BlockColors = [
    cc.hexToColor('#3376BA'),
    cc.hexToColor('#A24C38'),
    cc.hexToColor('#C79246'),
    cc.hexToColor('#83619A'),
    cc.hexToColor('#BE618F'),
    cc.hexToColor('#4DABC8')
];

let BlockConfig = require('BlockConfig');
let BingLog = require("BingLog");
let GameComponent = require("GameComponent");
let GameType = require("GameType");

let cls = cc.Class({

    extends:GameComponent,

    statics:{
      colorIndex : 0
    },

    properties:{
        type:{
            default:GameType.Block,
            override:true,
            visible:false
        },
        sp:cc.Sprite,
        numLable:cc.Label,
        count:0,
        color:cc.hexToColor('#3376BA'),
        particleFile:"",
    },

    //count
    init:function (count) {
        this.count = count;
        // BingLog.log("Block init:",this.count);
        let len = BlockColors.length;
        cls.colorIndex++;
        cls.colorIndex  = (cls.colorIndex)%len;
        // this.color = BlockColors[cls.colorIndex];

        if(count >0 ){
            let blockConfig  = this.getConfigByCount(count)
            let spName = blockConfig[0];
            this.particleFile = blockConfig[1];

            //
            // cc.loader.loadRes(pngname.split(".")[0], cc.SpriteFrame, function (err, spriteFrame) {
            //     self.sp.getComponent(cc.Sprite).spriteFrame = spriteFrame;
            // });

            //directly
            // var realUrl = cc.url.raw("resources/png/" + spName);
            // var texture = cc.textureCache.addImage(realUrl);
            // let spFrame = new cc.SpriteFrame(texture);
            // this.sp.spriteFrame = spFrame;

            // this.sp.spriteFrame = SpriteFrameCenter.getFrameByName("resources/png/block/" + spName);

            this.sp.spriteFrame = SpriteFrameCenter.getFrameFromAtlas("png/game",spName);
        }
        this.numLable.node.visible = count >0;

    },
    
    getConfigByCount:function (count) {
        let blocks = BlockConfig['blocks'];
        let len =  blocks.length;
        for(let i = 0; i < len; ++i){
            let config  =  blocks[i];
            if(config['min']<= count &&
                config['max'] >= count
            ){
                return [config['icon'],config['texiao']]
            }
        }

        console.error("you need config the block for ",count);
        return ['b_101.png','zk_lz.plist'];
    },

    onLoad:function () {
        // BingLog.log("tanke onLoad");
        // if(this.count  <= 0){
        //     this.numLable.node.visible = false;
        // }

        // this.sp.node.color = this.color;
    },
    
    
    update:function (dt) {

    },

    updateNode:function () {
        if(this.count  <= 0){
            this.numLable.node.visible = false;
        }
        else{
            this.numLable.node.visible = true;
            this.numLable.string = this.count.toString();
        }
    },

    //  碰撞后进行数目的变化
    // removeall 不论上面的数字，直接进行清空。
    updateCount:function (subcount,removeall=false) {

        //添加分数。
        let addScore = subcount;
        if(removeall){
            addScore = this.count;
        }
        Global.score +=addScore;
        this.count -= addScore;
        this.numLable.string = this.count.toString();
        return (this.count <= 0);
    },
    
    collisionAnimate:function () {

        let s1 = cc.scaleTo(0.1,1.2,1.2);
        let s2 = cc.scaleTo(0.05,1,1);
        let seq = cc.sequence(s1,s2);
        this.node.runAction(seq);
    }


});

module.exports = cls;