            // let tankFront = null;
            // for(let i = len ; i > 0; --i){
            //     tank = this.tanks[i].getComponent('Tank');
            //     tankFront = this.tanks[i-1].getComponent('Tank');
            //     // if(i==2){
            //     //     tank.points.unshift(...tankFront.points);
            //     //     // tank.updatePoints(this.collisionCount);
            //     // }else{
            //     //
            //     // }
            //
            //     let frontPt= this.tanks[i-1].getPosition();
            //     this.tanks[i].setPosition(frontPt.x,frontPt.y);
            //     tank.points = [];
            //     tank.points.push(...tankFront.points);
            // }
            //
            // console.log("......\n\n");
            // for(let i = 0 ; i <  this.tanks.length; ++i){
            //     tank = this.tanks[i].getComponent('Tank');
            //     let tmp = [];
            //     tmp.push(...tank.points);
            //     console.log("pt:",i,this.tanks[i].getPositionY(), tmp);
            // }
            // console.log("......\n\n");
            //
            // let remove = this.tanks.splice(1,1);
            // // remove[0].removeFromParent(true);
            // TankFactory.put(remove[0]);
