//负责更新Bullet的展现和变换。


let BingLog = require("BingLog");
let BulletFactory = require("BulletFactory");
let GameState = require("GameState");
let Global = require("Global");
let ItemType = require("ItemType");

cc.Class({
    extends:cc.Component,

    properties:{
        bullets:[cc.Node],
        gamecontroller:require('gamecontroller'),
        bulletsNodes:cc.Node, //全屏幕
    },

    onLoad:function () {
        this.bullets = [];
        this.tankController = this.node.getComponent('TankController');
        this.init();
    },

    init:function () {

    },

    addBullets:function (spName,pt,speed,relativespeed) {
        let  bulletNode =  BulletFactory.create(spName,speed,relativespeed);
        bulletNode.setPosition(pt);
        this.bullets.push(bulletNode);
        this.bulletsNodes.addChild(bulletNode);
    },

    update:function (dt) {

        let MaxDist = 500;//cc.winSize.height*0.5 - 20;
        //更新位置.
        let removes = [];
        let tankPtY=this.tankController.head.getPositionY();
        let tmpPtY = 0;
        let i = 0;
        for(; i < this.bullets.length; ++i){
            let bullet  = this.bullets[i];
            let Bullet = bullet.getComponent('Bullet');
            Bullet.updateNode(dt);

            //检测前面是否砖块，如果是砖块,砖块和字典消失。
            //如果是bar，子弹消失.。
            //tank bullet超过一定距离就消失。
            tmpPtY = bullet.getPositionY();
            if(tmpPtY - tankPtY > MaxDist){
                removes.push(i);
            }else{
                let ret = this.gamecontroller.tryBulletAtPt(bullet.getPosition());
                if(ret){
                    removes.push(i);
                }
            }
        }

        i = removes.length-1;
        for(; i > -1; --i){
            this.bullets[removes[i]].removeFromParent(true);
            this.bullets.splice(removes[i],1);
        }

    },

});