cc.Class({
    extends:cc.Component,

    onEnable:function () {

        console.log("create Block");
        let Factory = require("BlockFactory");
        for(let i = 0; i < 5; i++){
            let tank = Factory.create(i,(i%4+1).toString());
            this.node.addChild(tank);
            tank.position = cc.p( -2.5*140 + i*140+70,100);
        }
    }
});